export default{
	data(){
		return{
			name:'',
			phone:uni.getStorageSync('phone'),
			adminphone:'',
			storephone:'',
			address:'',
			opentime:'',
			img:"",
			des:'',
			typesList:[],
			classify:0
			
		}
	},
	onLoad(){
		uni.request({
			url:this.$bashUrl+'/index.php/home/Api/store_type',
			success:res=>{
				console.log(res)
				this.typesList = res.data
			}
		})
	},
	methods:{
		save(){
			if(this.name){
				if(this.storephone && this.storephone.length==11){
					if(this.des){
						if(this.address){
								if(this.opentime){
									if(this.img){
										console.log(this.img)
										uni.uploadFile({
											url:this.$bashUrl+'/index.php/home/Api/storeadd',
											method: "post",
											filePath:this.img[0],
											name:"img",
											formData:{
												name:this.name,
												phone:Number(this.storephone),
												user_phone:Number(this.adminphone),
												img:this.img,
												discount:this.des,
												type:this.typesList[this.classify].id,
												open_time:this.opentime,
												address:this.address
											},
											success:res=>{
												console.log(res)
												if(res.data==1){
													uni.showToast({
														title:"添加成功"
													})
													this.name =''
													this.storephone=''
													this.address=''
													this.adminphone=''
													this.opentime=''
													this.img='',
													this.des='',
													this.classify=0
													setTimeout(()=>{
														uni.navigateBack({
															delta:1
														})
													},2000)
													
												}else{
														uni.showToast({
															title:"添加失败"
														})
													}
												}
											})
							}else{
								uni.showToast({
									title:"请上传商家图片！",
									icon:'none'
								})
							}
						}else{
							uni.showToast({
								title:"请输入营业时间！",
								icon:'none'
							})
						}
					}else{
						uni.showToast({
							title:"请输入商家地址！",
							icon:'none'
						})
					}
				}else{
					uni.showToast({
						title:"请输入商家描述！",
						icon:'none'
					})
				}
				}else{
					uni.showToast({
						title:"请输入商铺电话！",
						icon:'none'
					})
				}
			}else{
				uni.showToast({
					title:"请输入商铺名字！",
					icon:'none'
				})
			}
	
	
	
		},
		bindChange(e){
			this.classify = e.detail.value 
		},
		upImg(type){
			uni.chooseImage({
				success: res =>{
					// 显示图片
					// 对传递过来的字符串进行改造,符合显示要求
					console.log(res)
						this.img = res.tempFilePaths
				},
				fail: function(res) {
				
				}
			});
			
		}
	}
}
